<template>
    <div class="l-model">
        <img class="l-logo" src="../assets/logo.png" alt="" />
        <h1 class="l-m-title">商家登陆</h1>
        <form action="" id="loginForm">
            <div class="rowView">
                <span class="l-subtitle">账号：</span>
                <input v-bind:class="showaccount?'l-tipsBorder':''" class="l-inputbox" type="text" name="email" @focus="hidetip"  v-model="account" />
            </div>
            <div class="rowView">
                <span class="l-subtitle">密码：</span>
                <input v-bind:class="showpassword?'l-tipsBorder':''" class="l-inputbox" type="password" maxlength="20" name="password" @focus="hidetip" v-model="password" />
            </div>
            <div class="rowView">
                <div class="l-tips">{{tips}}</div>
            </div>
            <div class="rowView" style="margin: 0;">
                <div class="l-read" style="justify-content: space-around;">
                    <div><input type="checkbox" name="rememberEmail" v-model="rememberAccount" />记住账号</div>
                    <div><input type="checkbox" name="rememberPassword" v-model="rememberPassword" />记住密码</div>
                </div>
            </div>
            <input class="l-submitBtn" type="button" value="登陆" @click="login"/>
            <router-link to="/Register"><input class="l-submitBtn" type="button" value="注册"/></router-link>
        </form>
        <h2 class="l-support">95创享科技技术支持</h2>
        <div v-show="mask" class="l-mask">
            <div class="l-mask-model">
                <div class="l-mask-modelChild">
                    <h3>提示</h3>
                    <p>{{masktext}}</p>
                    <div class="l-btnBox">
                        <button @click="hidemask" class="yes">确定</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return {
            showaccount:false,
            showpassword:false,
            tips:"",
            rememberAccount:true,
            rememberPassword:true,
            account:"",
            password:"",
            mask:false,
            masktext:""
        }
    },
    methods: {
        login:function(){
            if (!this.account) {
                this.showaccount = true;
                this.tips = "请输入邮箱"
            } else if(!this.password) {
                this.showpassword = true;
                this.tips = "请输入密码"
            } else {
                if(this.rememberAccount){ 
                    this.setCookie('user',this.account,30); //保存帐号到cookie，有效期30天
                }else{
                    this.delCookie('user')
                }
                if(this.rememberPassword){ 
                    this.setCookie('pswd',this.password,30); //保存密码到cookie，有效期30天
                }else{
                    this.delCookie('pswd')
                }
                var that = this;
                axios.post('http://139.196.73.183:8088/login', {
                    userName:this.account,
                    userPwd:this.password
                })
                .then(function (response) {
                    console.log(response)
                    if (response.data.errorCode=='0000') {
                        that.set_cache("userId",response.data.data.userId)
                        that.set_cache("userName",response.data.data.userName)
                        that.set_cache("token",response.data.data.token)
                        that.$router.push({path: 'Home'});
                    }else{
                        that.mask = true
                        that.masktext = response.data.errorMsg
                    }
                })
                .catch(function (error) {
                    console.log(error);
                }); 
            }
        },
        hidetip:function(){
            this.showaccount = false;
            this.showpassword = false;
            this.tips = ""
        },
        hidemask:function(){
            this.mask = false
        },
        //设置cookie
        setCookie:function(name,value,day){
            var date = new Date();
            date.setDate(date.getDate() + day);
            document.cookie = name + '=' + value + ';expires='+ date;
        },
        //获取cookie
        getCookie:function(name){
            var reg = RegExp(name+'=([^;]+)');
            var arr = document.cookie.match(reg);
            if(arr){
                return arr[1];
            }else{
                return '';
            }
        },
        //删除cookie
        delCookie:function(name){
            this.setCookie(name,null,-1);
        }
    },
    mounted: function () {
        this.$nextTick(function () {
           //页面初始化时，如果帐号密码cookie存在则填充
            if(this.getCookie('user')){
                this.account = this.getCookie('user')
            }else{
                // console.log("success11")
                this.rememberAccount = false
            }
            if(this.getCookie('pswd')){
                this.password = this.getCookie('pswd')
            }else{
                // console.log("success22")
                this.rememberPassword = false
            }
        })
    },
    created:function(){
    	var that = this;
		document.onkeydown=function(e){
			var key=window.event.keyCode;
			if(key==13){
				if (that.mask == true) {
					that.hidemask()
				} else{
					that.login();
				}
			}
		}
	}
}
</script>


