import Vue from 'vue'
import Router from 'vue-router'
import Reset from '@/components/Reset'
import Landr from '@/pages/Landr'
import Home from '@/pages/Home'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import test1 from '@/components/test1'
import test2 from '@/components/test2'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Landr',
      component: Landr,
      children: [
        {
          path: '/',
          component: Login,
          meta: {
            title: '登录'
          }
        },
        {
          path: '/Register',
          component: Register,
          meta: {
            title: '注册'
          }
        }
      ]
    },
    {
      path: '/Home',
      name: 'Home',
      component: Home,
      meta: {
        title: '消费者小程序-后台管理-商家配置'
      },
    },
    {
      path: '/Reset',
      name: 'Reset',
      component: Reset,
      meta: {
        title: '消费者小程序-后台管理-重置转发状态'
      }
    },
    {
      path: '/test1',
      name: 'test1',
      component: test1
    },
    {
      path: '/test2',
      name: 'test2',
      component: test2
    }
  ]
})



