<template>
    <div class="l-model">
        <img class="l-logo" src="../assets/logo.png" alt="" />
        <h1 class="l-m-title">商家注册</h1>
        <form action="" id="registerForm">
            <div class="rowView">
                <span class="l-subtitle">注册账号：</span>
                <input v-bind:class="showaccount?'l-tipsBorder':''" class="l-inputbox" @focus="hidetip" type="text" name="registerEmail" v-model="account" />
            </div>
            <div class="rowView">
                <span class="l-subtitle">密码：</span>
                <input v-bind:class="showpassword?'l-tipsBorder':''" class="l-inputbox" @focus="hidetip" type="password" placeholder="密码长度为6-20位" maxlength="20" v-model="password" />
            </div>
            <div class="rowView">
                <span class="l-subtitle">重复密码：</span>
                <input v-bind:class="showpasswordAgain?'l-tipsBorder':''" class="l-inputbox" type="password" @focus="hidetip" maxlength="20" v-model="passwordAgain" />
            </div>
            <div class="rowView">
                <div class="l-tips">{{tips}}</div>
            </div>
            <div class="rowView" style="margin: 0;">
                <div class="l-read">
                    <input type="checkbox" @change="hidetip" name="" v-model="read" />我已阅读和知晓<router-link to="">平台说明</router-link>及<router-link to="">免责声明</router-link>
                </div>
            </div>
            <input class="l-submitBtn" type="button" value="立即注册" @click="register"/>
            <div class="rowView" style="margin-top: 10px;margin-bottom: 0;">
                <div class="l-backlogin">已有账号， <router-link to="/">返回登陆</router-link></div>
            </div>
        </form>
        <h2 class="l-support">95创享科技技术支持</h2>
        <div v-show="mask" class="l-mask">
            <div class="l-mask-model">
                <div class="l-mask-modelChild">
                    <h3>提示</h3>
                    <p>{{masktext}}</p>
                    <div class="l-btnBox">
                        <button @click="hidemask" class="yes">确定</button>
                        <router-link style="margin-left:35px" v-show="backlogin" to="/"><button>返回登陆</button></router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
    data(){
        return{
            mask:false,
            masktext:"",
            showaccount:false,
            showpassword:false,
            showpasswordAgain:false,
            tips:"",
            backlogin:false,
            account:"",
            password:"",
            passwordAgain:"",
            read:true
        }
    },
    methods:{
        register:function(){
            if (!this.account) {
                this.showaccount = true;
                this.tips = "请输入注册邮箱"
            } else if(!this.password) {
                this.showpassword = true;
                this.tips = "请输入密码"
            } else if(this.password.length<6) {
                this.showpassword = true;
                this.tips = "密码不能少于六位"
            } else if(!this.passwordAgain) {
                this.showpasswordAgain = true;
                this.tips = "请输入重复密码"
            } else if(!(this.password == this.passwordAgain)) {
                this.showpasswordAgain = true;
                this.tips = "密码与重复密码不相同"
            } else if(!this.read) {
                this.tips = "请先确认阅读平台说明及免责声明"
            } else {
                var that = this;
                axios.post('http://139.196.73.183:8088/register', {
                    userName:this.account,
                    userPwd:this.password
                })
                .then(function (response) {
                    console.log(response)
                    if (response.data.errorCode=='0000') {
                        that.account = "",
                        that.password = "",
                        that.passwordAgain = "",
                        that.mask = true,
                        that.masktext = "注册成功!",
                        that.backlogin = true
                    }else{
                        that.mask = true
                        that.masktext = response.data.errorMsg
                    }
                })
                .catch(function (error) {
                    console.log(error);
                }); 
            }
        },
        hidetip:function(){
            this.showaccount = false;
            this.showpassword = false;
            this.showpasswordAgain = false;
            this.tips = ""
        },
        hidemask:function(){
            this.mask = false
        },
    }
}
</script>



