<template>
    <div>
        <div class="l-head">
            <div class="l-nav">
                <div class="l-userInfo"><span class="l-userName">{{userName}}</span><span class="l-sep">|</span><router-link class="l-logOff" @click.native="signOut" to="/">注销</router-link></div>
            </div>
        </div>
        <div :class="isHas ? 'topBarHeight' : ''" class="l-topBar">您已设置过内容</div>
        <div class="l-bg">
			<div class="l-container">
				<form action="" id="">
					<div class="l-srction-tips">基本信息</div>
					<div class="l-shopInfo">
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅名称</span>
							<input class="l-formInput" type="text" v-model="shopName" />
							<span class="l-formInputContent">限20个字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅地区</span>
							<select class="l-formSelect" v-model="shopArea">
                                <option v-for="item in shopAreaList" :value="item.id" :key="item.id">{{item.areaName}}</option>
							</select>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅地址</span>
							<input class="l-formInput" type="text" v-model="shopAddress" />
							<span class="l-formInputContent">限100个字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>联系方式</span>
							<input class="l-formMiddleMiniInput" type="number" v-model="contactWayList.first" />-<input style="margin-left: 10px;" class="l-formMiddleMiniInput" type="number"  v-model="contactWayList.second" />-<input class="l-formMiddleSmallerInput" type="number" v-model="contactWayList.last" />
							<span class="l-formInputContent">限填数字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>营业时间</span>
							<input class="l-formInput" type="text" v-model="openTime" />
							<span class="l-formInputContent">限50个字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅设施</span>
							<div class="l-columnView">
								<div class="l-formInputSmall-box indoorDevice">
									<div class="l-formInputSmallView" v-for="(item,index) in indoorDeviceList" :key="index">
										<input class="l-formInputSmall" type="text" v-model="item.name" />
										<div class="l-InputSmallClose" :data-index="index" @click="deleteIndoorDevice">x</div>
									</div>
								</div>
								<div class="l-childRowView">
									<input class="l-smallButton" type="button" @click="addIndoorDevice" value="+" />
								</div>
							</div>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>美食种类</span>
							<select class="l-formSelect" v-model="classId">
								<option v-for="item in classIdList" :key="item.id" :value="item.id">{{item.name}}</option>
							</select>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>人均消费</span>
							<input class="l-formInput" type="number" v-model="averageConsume" />
							<span class="l-formInputContent">/ 人，限填数字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅标签</span>
							<div class="l-columnView">
								<div class="l-formInputSmall-box features">
									<div class="l-formInputSmallView" v-for="(item,index) in featuresList" :key="index">
										<input class="l-formInputSmall" placeholder="限六个字" type="text"  v-model="item.name" />
										<div class="l-InputSmallClose" :data-index="index" @click="deleteFeatures">x</div>
									</div>
								</div>
								<div class="l-childRowView">
									<input class="l-smallButton featuresBtn" type="button" @click="addFeatures" value="+" />
								</div>
							</div>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle redcolor"><span class="l-mustTips">*</span>餐厅简介</span>
							<textarea class="l-formTextarea" cols="30" rows="3" type="text" v-model="simpleIntro" ></textarea>
							<span class="l-formInputContent">限200个字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>餐厅介绍</span>
							<textarea class="l-formTextarea" cols="30" rows="3" type="text" v-model="shopIntro" ></textarea>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>人气商家</span>
							<select class="l-formSelect" v-model="isHot">
								<option value="0">否</option>
								<option value="1">是</option>
							</select>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle">排序值</span>
							<input class="l-formInput" type="number" onblur="" v-model="orderValue" />
							<span class="l-formInputContent">限填数字</span>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle">经度</span>
							<input class="l-formInput" type="text" v-model="longitude" />
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle">纬度</span>
							<input class="l-formInput" type="text" v-model="latitude" />
						</div>
					</div>
					<div class="l-srction-tips">餐厅顶图</div>
					<div class="l-shopInfo">
						<div class="l-formRowView pr">
							<span class="l-formTitle"><span class="l-mustTips">*</span>顶部图片</span>
							<div class="vue-uploader">
							    <div class="file-list">
							      <section v-if="topImgUpload" class="file-item draggable-item">
							        <img :src="topImgUpload" alt="" ondragstart="return false;">
							        <span class="btn delBtn" @click="remove(topImgBtn)">删除</span>
							      </section>
							      <section v-if="!topImgUpload" class="file-item">
							        <section class="upload-func" v-if="topImg.length>0">
							          <div class="uploadImgName">{{topImg[0].name+"("+topImg[0].size/1000+"kb)"}}</div>
							          <div style="background: #eee;">
							            <section class="progress-bar" :style="'width:' + (percent * 100) + '%'" >{{(percent * 100) + '%'}}</section>
							          </div>
							        </section>
							        <div v-if="!topImg.length>0" class="selectionBox">+
							    		<input type="file" @change="add(topImgBtn)" :id="topImgBtn" accept="image/jpg,image/jpeg,image/png,image/gif">
							        </div>
							        <div class="operation-box">
							          <button class="btn bigBtn" @click="ossUpload(topImg,'shopImg/topImg')">上传</button>
							        </div>
							        <span class="l-formInputContent">图片大小不可超过1M，否则将选取失败</span>
							      </section>
							    </div>
							</div>
						</div>
					</div>
					<div class="l-srction-tips">商家详情方形图</div>
					<div class="l-shopInfo">
						<div class="l-formRowView pr">
							<span class="l-formTitle"><span class="l-mustTips">*</span>商家详情图</span>
							<div class="vue-uploader">
							    <div class="file-list">
							      <section v-if="introImagesUpload" class="file-item draggable-item">
							        <img :src="introImagesUpload" alt="" ondragstart="return false;">
							        <span class="btn delBtn" @click="remove(introImagesBtn)">删除</span>
							      </section>
							      <section v-if="!introImagesUpload" class="file-item">
							        <section class="upload-func" v-if="introImages.length>0">
							          <div class="uploadImgName">{{topImg[0].name+"("+introImages[0].size/1000+"kb)"}}</div>
							          <div style="background: #eee;">
							            <section class="progress-bar" :style="'width:' + (percent * 100) + '%'" >{{(percent * 100) + '%'}}</section>
							          </div>
							        </section>
							        <div v-if="!introImages.length>0" class="selectionBox">+
							    		<input type="file" @change="add(introImagesBtn)" :id="introImagesBtn" accept="image/jpg,image/jpeg,image/png,image/gif">
							        </div>
							        <div class="operation-box">
							          <button class="btn bigBtn" @click="ossUpload(introImages,'shopImg/introImages')">上传</button>
							        </div>
							        <span class="l-formInputContent">图片大小不可超过1M，否则将选取失败</span>
							      </section>
							    </div>
							</div>
						</div>
					</div>
					<div class="l-srction-tips">特色推荐</div>
					<div class="l-shopInfo">
						<div id="dishesImg" class="l-formRowView pr borderBox marginNo paddingTB">
							<section v-for="(item,index) in specialDishesUpload" :key="index" class="file-item draggable-item" style="margin-right: 30px;margin-bottom: 30px;">
								<img style="width: 265px;" :src="item.imagePath" alt="" ondragstart="return false;">
								<span style="left: 211px;" class="btn delBtn" @click="remove(specialDishesBtn,index)">删除</span>
								<div class='dishesbox'>
									<div class='dishesView'>
										<span class='dishesTitle'>名称：</span><input class='l-formMiddleInput' type='text' v-model="item.dishName">
									</div>
									<div class='dishesView'>
										<span class='dishesTitle'>价格：</span><input class='l-formMiddleMiniInput' type='number' v-model='item.dishPrice'>/<input style='margin-left: 10px;' class='l-formMiddleMiniInput' type='text' v-model='item.dishUnit'><span class='dishesTitle'>单位</span>
									</div>
								</div>
							</section>
							<div class="vue-uploader" v-if="specialDishesUpload.length<9">
							    <div class="file-list">
							      <section class="file-item">
							        <section class="upload-func" v-if="specialDishes.length>0">
							          <div class="uploadImgName">{{specialDishes[0].name+"("+specialDishes[0].size/1000+"kb)"}}</div>
							          <div style="background: #eee;">
							            <section class="progress-bar" :style="'width:' + (percent * 100) + '%'" >{{(percent * 100) + '%'}}</section>
							          </div>
							        </section>
							        <div v-if="!specialDishes.length>0" class="selectionBox">+
							    		<input type="file" @change="add(specialDishesBtn)" :id="specialDishesBtn" accept="image/jpg,image/jpeg,image/png,image/gif">
							        </div>
							        <div class="operation-box">
							          <button class="btn bigBtn" @click="ossUpload(specialDishes,'shopImg')">上传</button>
							        </div>
							      </section>
							    </div>
							</div>
						</div>
						<span class="l-formInputContent" style="margin-top: 5px;">最多添加9个菜品，图片大小不可超过1M，否则将选取失败</span>
					</div>
					<div class="l-srction-tips">转发优惠</div>
					<div class="l-shopInfo">
						<div v-for="(item,index) in couponList" :key="item.id" class="l-formBox">
							<span class="l-formCloseBtn" :data-index="index" @click="deleteCoupon">删除</span>
							<div class="l-formRowView">
								<span class="l-formTitle">优惠形式</span>
								<select class="l-formSelect" v-model="item.couponType">
									<option value="0">代金券</option>
									<option value="1">折扣券</option>
									<option value="2">赠品券</option>
								</select>
							</div>
							<div class="l-formRowView">
								<span class="l-formTitle">优惠内容</span>
								<input class="l-formInput" placeholder="限50个字" type="text" v-model="item.couponValue" />
								<span class="l-formInputContent">{{item.couponType==0?"元代金券面值":item.couponType==1?"%折扣券比例":"每份赠品内容"}}</span>
							</div>
							<div class="l-formRowView">
								<span class="l-formTitle">转发数量</span>
								<input class="l-formInput" type="number" v-model="item.exchangeTimes" />
								<span class="l-formInputContent">次转发兑换一次</span>
							</div>
						</div>
						<div class="l-formRowView">
							<span class="l-formTitle"></span>
							<input class="l-formButton" type="button" @click="addCoupon" value="+ 增加一种优惠设定" />
						</div>
					</div>
					<div class="l-srction-tips">使用规则</div>
					<div class="l-shopInfo">
						<div class="l-formRowView">
							<span class="l-formTitle">提示内容</span>
							<textarea class="l-formTextarea" type="text" v-model="warmPrompt"></textarea>
							<span class="l-formInputContent">限200个字</span>
						</div>
					</div>
					<div class="l-srction-tips">分享卡</div>
					<div class="l-shopInfo">
						<div class="l-formRowView">
							<span class="l-formTitle"><span class="l-mustTips">*</span>分享卡标题</span>
							<input class="l-formInput" type="text" v-model="shareTitle" />
						</div>
						<div class="l-formRowView pr">
							<span class="l-formTitle"><span class="l-mustTips">*</span>分享卡图片</span>
							<div class="vue-uploader">
							    <div class="file-list">
							      <section v-if="shareImgUpload" class="file-item draggable-item">
							        <img style="height: 180px;" :src="shareImgUpload" alt="" ondragstart="return false;">
							        <span class="btn delBtn" @click="remove(shareImgBtn)">删除</span>
							      </section>
							      <section v-if="!shareImgUpload" class="file-item">
							        <section class="upload-func" v-if="shareImg.length>0">
							          <div class="uploadImgName">{{shareImg[0].name+"("+shareImg[0].size/1000+"kb)"}}</div>
							          <div style="background: #eee;">
							            <section class="progress-bar" :style="'width:' + (percent * 100) + '%'" >{{(percent * 100) + '%'}}</section>
							          </div>
							        </section>
							        <div v-if="!shareImg.length>0" class="selectionBox" style="height: 180px;">+
							    		<input type="file" @change="add(shareImgBtn)" :id="shareImgBtn" accept="image/jpg,image/jpeg,image/png,image/gif">
							        </div>
							        <div class="operation-box">
							          <button class="btn bigBtn" @click="ossUpload(shareImg,'shopImg/shareImg')">上传</button>
							        </div>
							        <span class="l-formInputContent">图片大小不可超过1M，否则将选取失败</span>
							      </section>
							    </div>
							</div>
						</div>
					</div>
					<div class="l-srction-tips">觅食Tips</div>
					<div class="l-shopInfo">
						<div class="l-formRowView">
							<span class="l-formTitle">TIPS</span>
							<textarea class="l-formTextarea" type="text" v-model="flowContent" value="" ></textarea>
							<span class="l-formInputContent">限200个字</span>
						</div>
					</div>
					<div class="l-srction-tips">微信相关</div>
					<div class="l-shopInfo">
						<div class="l-formRowView">
							<span class="l-formTitle">公众号文章地址</span>
							<input class="l-formInput" type="text" v-model="articleUrl" value="" />
						</div>
						<div v-if="shopId" class="xcxcode">
							<div class="l-formRowView">
								<span class="l-formTitle">小程序码宽度</span>
								<input class="l-formInputSmall marginBotNo" type="number" placeholder="280~800" @blur="ctrlCode" v-model="xcxcode" />
								<input class="l-codeButton" type="button" @click="makeCode" value="点击生成小程序码" />
							</div>
							<div class="l-formRowView">
								<span class="l-formTitle"></span>
								<img class="xcxcodeImg" :src="xcxcodeImgSrc"/>
							</div> 
						</div>
					</div>
					<input v-if="!shopId" class="l-formSubmitBtn" type="button" value="确定" @keyup.enter="hidemask" @click="addShop"/>
					<input v-if="shopId" class="l-formSubmitBtn" type="button" value="修改" @click="modeifyShop"/>
				</form>
			</div>
		</div>
        <div v-if="mask" class="l-mask">
            <div class="l-mask-model">
                <div class="l-mask-modelChild">
                    <h3>提示</h3>
                    <p>{{masktext}}</p>
                    <div class="l-btnBox">
                        <button v-if="yesBtn" @click="hidemask" class="yes">确定</button>
                        <router-link v-if="!yesBtn" to="/"><button class="backLogin">返回登陆</button></router-link>
                    </div>
                </div>
            </div>
        </div>
        <!--加载框-->
		<div v-show="loading" class="l-loading">
			<img src="../assets/loading.gif"/>
		</div>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return{
					mask:false,
					masktext:"",
					loading:false,
					userId : "",
					userName : "",
					token : "",
					shopId : "",
					shopName:"",
					shopArea:0,
					shopAreaList:[{id:0,areaName:"温哥华"},{id:1,areaName:"本拿比"},{id:2,areaName:"列治文"},{id:3,areaName:"高贵林"}],
					shopAddress:"",
					contactWayList:{
							first:"",
							second:"",
							last:""
					},
					contactWay:"",
					openTime:"",
					indoorDevice:"",
					indoorDeviceList:[{name:""}],
					classId:0,
					classIdList:[{id:0,name:"川湘麻辣"},{id:1,name:"锅物料理"},{id:2,name:"饮品甜点"},{id:3,name:"北方菜"},{id:4,name:"本帮江浙菜"},{id:5,name:"小吃简餐"},{id:6,name:"海鲜"},{id:7,name:"粤菜"},{id:8,name:"面食"}],
					averageConsume:"",
					features:"",
					featuresList:[{name:""}],
					simpleIntro:"",
					shopIntro:"",
					isHot:0,
					orderValue:"",
					longitude:"",
					latitude:"",
					couponList:[{
						couponType:'0',
						couponValue:"",
						exchangeTimes:1
					}],
					coupon:[],
					warmPrompt:"",
					shareTitle:"",
					flowContent:"",
					articleUrl:"",
					xcxcode:"",
					xcxcodeImgSrc:"",
					topImgBtn:"topImgBtn",
					topImg: [],
	        topImgUpload:"",
	        introImagesBtn:"introImagesBtn",
	        introImages:[],
	        introImagesUpload:"",
	        shareImg:[],
	        shareImgUpload:"",
	        shareImgBtn:"shareImgBtn",
	        specialDishes:[],
	        specialDishesUpload:[],
	        specialDishesBtn:"specialDishesBtn",
	        percent: 0,
	        yesBtn:true,
	        isHas:false
        }
    },
    methods:{
        // 显示弹出框
        showmask:function(masktext){
            this.mask = true
            this.masktext = masktext
        },
        // 隐藏弹出框
        hidemask:function(){
            this.mask = false
        },
        // 添加设施
        addIndoorDevice:function(){
            this.indoorDeviceList.push({name:""})
        },
        // 删除设施
        deleteIndoorDevice:function(e){
            if (this.indoorDeviceList.length>1) {
                this.indoorDeviceList.splice(e.srcElement.dataset.index,1)
            } else {
                this.showmask("至少添加一种餐厅设施！")
            }
        },
        // 添加标签
        addFeatures:function(){
            this.featuresList.push({name:""})
        },
        // 删除标签
        deleteFeatures:function(e){
            if (this.featuresList.length>1) {
                this.featuresList.splice(e.srcElement.dataset.index,1)
            } else {
                this.showmask("至少添加一种标签！")
            }
        },
        // 添加优惠券
        addCoupon:function(){
            this.couponList.push({couponType:0,couponValue:"",exchangeTimes:1})
        },
        // 删除优惠券
        deleteCoupon:function(e){
            if (this.couponList.length>1) {
                this.couponList.splice(e.srcElement.dataset.index,1)
            } else {
                this.showmask("至少设置一种优惠券！")
            }
		},
		// 控制小程序码大小
		ctrlCode:function(){
			if(this.xcxcode<280){
				this.xcxcode = 280;
			}
			if(this.xcxcode>800){
				this.xcxcode = 800;
			}
		},
		// 生成小程序码
		makeCode:function(){
			if (!this.xcxcode) {
				this.showmask("请输入小程序码需要大小（单位：px）")
			} else{
				this.xcxcodeImgSrc = "http://139.196.73.183:8088/qrcode/getwxcode?shopId="+this.shopId+"&width="+this.xcxcode
			}
		},
		// 注销
		signOut:function(){
			this.remove_cache("token")
		},
		//限制长度
		checkWord:function(len,parameter){
//			console.log(parameter)
			//len为英文字符的个数，中文自动为其一般数量,evt是欲检测的对象
			var myLen = 0;
			var i = 0;
			for(i=0; i<parameter.length&&myLen<=len; i++){
				if(parameter.charCodeAt(i)>0&&parameter.charCodeAt(i)<128)
					myLen++;
				else
					myLen+=2;
			}
			if(myLen>len){
				parameter = parameter.substring(0,i-1);
			}
			return parameter
		},
		//添加图片
		add(id) {
			const list = document.getElementById(id).files
	        for (let i = 0; i < list.length; i++) {
	          if (list[i].size<1048576) {
	            if (id == 'topImgBtn') {
	            	this.topImg.push(list[i])
	            } else if(id == 'introImagesBtn'){
	            	this.introImages.push(list[i])
	            } else if(id == 'shareImgBtn'){
	            	this.shareImg.push(list[i])
	            } else if(id == 'specialDishesBtn'){
