	    ossUpload:function(localImg,path) {
//	        console.log(localImg)
	        var that=this
	        // 这里是OSS
	        const client = new OSS.Wrapper({
		        region: 'oss-us-west-1', 
		        accessKeyId: 'LTAICdHS3UrXF0jt',
		        accessKeySecret: '6W3Ks3AVctu7h3Qd8wtJeYqNmcMdTE',
		        bucket: 'zhuanyoyo',
		        secure:true
	        });
	        const fNum = localImg;
	        for(var i=0;i<fNum.length;i++){
	        	var f=fNum[i]
	            const Name=f.name
	            const suffix = Name.substr(Name.indexOf("."));
	            const obj=this.timestamp();
	            const storeAs = path+'/'+obj+suffix // 路径+时间戳+后缀名
	            client.multipartUpload(storeAs, f,{progress: function* (p) {
	            	that.percent = p;
	          	}}).then(function (result){
//	            	console.log(result.res.requestUrls)
	            	var uploadImg = (result.res.requestUrls[0].indexOf("?") != -1)?result.res.requestUrls[0].split("?")[0]:result.res.requestUrls[0]
	            	if(path=='shopImg/topImg'){
	            		that.topImgUpload = uploadImg;
	            		that.percent = 0;
	            	}else if(path=='shopImg/introImages'){
	            		that.introImagesUpload = uploadImg;
	            		that.percent = 0;
	            	}else if(path=='shopImg/shareImg'){
	            		that.shareImgUpload = uploadImg;
	            		that.percent = 0;
	            	}else if(path=='shopImg'){
	            		that.specialDishesUpload.push({imagePath:uploadImg,dishName:"",dishPrice:"",dishUnit:""});
	            		that.specialDishes = [];
	            		that.percent = 0;
	            	}
	          	}).catch((err) => {
	            	console.log(err);
	          	});
	        }
	    },
	    // 时间戳
	    timestamp:function(){
	        const time = new Date(); 
	        const y = time.getFullYear(); 
	        const m = time.getMonth()+1; 
	        const d = time.getDate(); 
	        const h = time.getHours(); 
	        const mm = time.getMinutes(); 
	        const s = time.getSeconds(); 
	        const ms = time.getMilliseconds(); 
	        return ""+y+this.Add0(m)+this.Add0(d)+this.Add0(h)+this.Add0(mm)+this.Add0(s)+this.Add0(ms); 
	    },
	    Add0:function(m){
	        return m<10?'0'+m : m; 
	    } ,
	    remove(id,index) {
	    	if (id == 'topImgBtn') {
	    		this.topImgUpload = ""
	    		this.topImg = []
            } else if(id == 'introImagesBtn'){
            	this.introImagesUpload = ""
	    		this.introImages = []
            } else if(id == 'shareImgBtn'){
            	this.shareImgUpload = ""
	    		this.shareImg = []
            } else if(id == 'specialDishesBtn'){
            	this.specialDishesUpload.splice(index, 1)
            }
	    },
