    mounted:function(){
        this.userId = this.get_cache("userId")
        this.userName = this.get_cache("userName")
		this.token = "Bearer:"+this.get_cache("token")
		if (this.get_cache("token")) {
			var that = this;
			axios.get('http://139.196.73.183:8088/shop/selectbyuserid?userId='+this.userId,{headers:{'Authorization':this.token}})
			.then(function(response){
				// console.log(response)
				if (response.data.errorCode=='0000') {
					if(response.data.data.length>0){
						that.isHas = true;
						setTimeout(function(){
							that.isHas = false;
						},5000)
						document.documentElement.scrollTop=0;
						that.shopId = response.data.data[0].shopId;
						that.shopName = response.data.data[0].shopName;
						that.shopArea = response.data.data[0].shopArea;
						that.shopAddress = response.data.data[0].shopAddress;
						that.contactWayList.first = response.data.data[0].contactWay.split('-')[0];
						that.contactWayList.second = response.data.data[0].contactWay.split('-')[1];
						that.contactWayList.last = response.data.data[0].contactWay.split('-')[2];
						that.openTime = response.data.data[0].openTime;
						that.indoorDeviceList = [];
						for (let i = 0; i < response.data.data[0].indoorDevice.split(';').length; i++) {
							that.indoorDeviceList.push({name:response.data.data[0].indoorDevice.split(';')[i]})
						}
						that.classId = response.data.data[0].classId;
						that.averageConsume = response.data.data[0].averageConsume;
						that.featuresList = [];
						for (let i = 0; i < response.data.data[0].features.split(';').length; i++) {
							that.featuresList.push({name:response.data.data[0].features.split(';')[i]})
						}
						that.simpleIntro = response.data.data[0].simpleIntro;
						that.shopIntro = response.data.data[0].shopIntro;
						that.isHot = response.data.data[0].isHot;
						that.orderValue = response.data.data[0].orderValue;
						that.longitude = response.data.data[0].longitude;
						that.latitude = response.data.data[0].latitude;
						that.topImgUpload = response.data.data[0].topImagePath;
						that.introImagesUpload = response.data.data[0].introImages;
						that.shareImgUpload = response.data.data[0].shareImagePath;
						that.specialDishesUpload = response.data.data[0].specialDishes;
						that.couponList = [];
						for (let i = 0; i < response.data.data[0].couponList.length; i++) {
							that.couponList.push({
								couponType:response.data.data[0].couponList[i].couponType,
								couponValue:response.data.data[0].couponList[i].couponValue,
								exchangeTimes:response.data.data[0].couponList[i].exchangeTimes
							})
						}
						that.warmPrompt = response.data.data[0].warmPrompt;
						that.shareTitle = response.data.data[0].shareTitle;
						that.flowContent = response.data.data[0].flowContent;
						that.articleUrl = response.data.data[0].articleUrl;
					}
				} else if (response.data.errorCode=='0007'){
					that.showmask("登陆已过期，请重新登陆")
					that.yesBtn = false;
				} else{
					that.showmask(res.errorMsg)
				}
			})
