        },
        featuresList:{
            handler: function (newVal,oldVal){
            	for (var i = 0; i < this.featuresList.length; i++) {
            		this.featuresList[i].name = this.checkWord(12,this.featuresList[i].name)
            	}
            },
            deep: true
        },
