    created:function(){
    	var that = this;
		document.onkeydown=function(e){
			var key=window.event.keyCode;
			if(key==13){
				if (that.mask == true) {
					that.hidemask()
				}
			}
		}
	}
}
</script>
<style>
.file-list:after {
	content: '';
	display: block;
	clear: both;
	visibility: hidden;
	line-height: 0;
	height: 0;
	font-size: 0;
}

.file-item {
	float: left;
	position: relative;
	text-align: left;
}

.file-item img {
	width: 240px;
	height: 100px;
}
.vue-uploader .add {
	width: 150px;
	height: 150px;
	float: left;
	text-align: center;
	line-height: 150px;
	font-size: 30px;
	cursor: pointer;
	background: #eee;
	background-size: 100% 100%;
}

.vue-uploader .upload-func {
	display: flex;
	flex-direction: column;
	padding: 10px 0;
	margin: 0px;
}

.vue-uploader .upload-func .progress-bar {
	flex-grow: 1;
}

.vue-uploader .upload-func .progress-bar section {
	margin-top: 5px;
	background: #00b4aa;
	border-radius: 3px;
	text-align: center;
	color: #fff;
	font-size: 12px;
	transition: all .5s ease;
}

.vue-uploader .upload-func .operation-box {
	flex-grow: 0;
	padding-left: 10px;
}

.vue-uploader .upload-func .operation-box button {
	padding: 4px 12px;
	color: #fff;
	background: #007ACC;
	border: none;
	border-radius: 2px;
	cursor: pointer;
}

.selectionBox>input[type="file"] {
	position: absolute;
	width: 100%;
	background: red;
	height: 100%;
	opacity: 0;
}

</style>




